import { clamp } from './clamp';

export const modulate = (value, [inMin, inMax], [outMin, outMax], limit) => {
  const map = ((value - inMin) * (outMax - outMin)) / (inMax - inMin) + outMin;
  if (limit) {
    return clamp(
      map,
      outMin < outMax ? outMin : outMax,
      outMax > outMin ? outMax : outMin
    );
  }
  return map;
};
