import { Suspense, useEffect, useState } from "react";
import { BrowserRouter, Route, Routes } from "react-router-dom";
import { AnimatePresence } from "framer-motion";
import { ThemeProvider } from "styled-components";
import { StyledGrid as Grid } from "../lrnz/styled/index";
// import Handlers from "../lrnz/react-handlers/index";
import Homepage from "./Homepage";
// import Contacts from "./Contacts";
// import Gallery from "./Gallery";
// import Detail from "./Detail";
import { Provider } from "../context";
import GlobalStyles from "../styles/Global";
import theme from "../styles/theme";

import AnimatedSwitch from "../components/AnimatedSwitch";
import CookieBanner from "../components/CookieBanner";
import Loader from "../components/Loader";
import Interface from "../components/Interface";
import Cursor from "../components/Cursor";

const Handlers = ({ children }) => <>{children}</>;

const App = () => {
  const [showLoader, setShowLoader] = useState(true);

  useEffect(() => {
    setTimeout(() => {
      setShowLoader(false);
    }, 6000);
  }, []);

  return (
    <Handlers preferChangeOrientation debounce={0}>
      <ThemeProvider theme={theme}>
        <GlobalStyles />
        <Suspense fallback={null}>
          <Provider>
            <Grid masked>
              <Cursor />
              <BrowserRouter>
                <AnimatePresence>
                  {showLoader && <Loader key={0} />}
                  {!showLoader && (
                    <><Routes>
                      {/* <AnimatedSwitch> */}
                        <Route path="/" element={<Homepage />} />
                        {/* <Route
                          path="/:template(creations|interview)"
                          component={Detail}
                        />
                        <Route
                          path="/gallery/:filter(creations|interview)?"
                          component={Gallery}
                        />
                        <Route path="/contacts" component={Contacts} /> */}
                      {/* </AnimatedSwitch> */}
                      </Routes>
                      <Interface />
                    </>
                  )}
                </AnimatePresence>
                {!showLoader && <CookieBanner />}
                </BrowserRouter>
            </Grid>
          </Provider>
        </Suspense>
      </ThemeProvider>
    </Handlers>
  );
};

export default App;
