import { css } from 'styled-components';
import defaultTheme from '../theme';

/*
The breakpoints util can be invoked inside a createGlobalStyles, where theme is equal to undefined.
We need to set the theme default equal to {} for uniform the behavior with all the other scenarios
*/

export const breakpoints = (breakpoint) => (generator) => css`${({ theme = {} }) => {
  const mediaquery = theme.mediaquery || defaultTheme.mediaquery;
  return mediaquery[breakpoint];
}} { ${generator} }`;

export const styles = (styleGenerator) => (props) => {
  const mediaquery = props.theme.mediaquery || defaultTheme.mediaquery;
  const keys = Object.keys(mediaquery);

  return keys.reduce((
    acc,
    breakpoint,
  ) => {
    if (!props[breakpoint] && props[breakpoint] !== 0) return acc;

    return [
      ...acc,
      breakpoints(breakpoint)(styleGenerator({
        ...props[breakpoint],
        theme: props.theme,
      })),
    ];
  }, [styleGenerator(props)]);
};

export const mapToBreakpoints = (
  types,
  mediaquery = defaultTheme.mediaquery,
) => Object.keys(mediaquery).reduce((
  acc,
  breakpoint,
) => Object.assign(acc, { [breakpoint]: types }), {});
