import PropTypes from 'prop-types';
import styled from 'styled-components';

import {
  normalizeFlexAlignValue,
  isObject,
  mapNumberToUnit,
} from '../../utils';
import { styles as responsiveStyles } from '../../responsive';

const stylesTemplate = ({ wrapped, column, reverse, align, justify }) => {
  const reverseDirection = isObject(reverse) ? reverse.direction : reverse;

  const reverseWrap = isObject(reverse) && reverse.wrap;

  const flexDirection =
    typeof column === 'boolean' || reverseDirection
      ? `${column ? 'column' : 'row'}${reverseDirection ? '-reverse' : ''}`
      : null;

  // eslint-disable-next-line no-nested-ternary
  const flexWrap =
    wrapped === false
      ? 'nowrap'
      : wrapped === true
      ? `wrap${reverseWrap ? '-reverse' : ''}`
      : null;

  const margin = 'calc(var(--grid-gutter) * -0.5)';

  return {
    marginLeft: margin,
    marginRight: margin,
    flexWrap,
    flexDirection,
    alignItems: normalizeFlexAlignValue(align),
    justifyContent: normalizeFlexAlignValue(justify),
  };
};

export const StyledRow = styled.div`
  display: flex;
  box-sizing: border-box;
  ${responsiveStyles(stylesTemplate)}
`;

StyledRow.propTypes = {
  /**
   * Set flex-wrap to wrap
   */
  wrapped: PropTypes.bool,
  /**
   * Set flex-direction as column based
   */
  column: PropTypes.bool,
  /**
   * Reverse flex-direction and/or flex-wrap
   */
  reverse: PropTypes.oneOfType([
    PropTypes.bool,
    PropTypes.shape({
      direction: PropTypes.bool,
      wrap: PropTypes.bool,
    }),
  ]),
  /**
   * Flex align-items
   */
  align: PropTypes.oneOf(['start', 'center', 'end', 'baseline']),
  /**
   * Flex justify-content
   */
  justify: PropTypes.oneOf([
    'start',
    'center',
    'end',
    'between',
    'around',
    'evenly',
  ]),
};

StyledRow.defaultProps = {
  wrapped: true,
  column: null,
  reverse: null,
  align: null,
  justify: null,
};

StyledRow.displayName = 'Row';

export default StyledRow;
