/* eslint-disable no-nested-ternary */
import PropTypes from 'prop-types';
import styled from 'styled-components';
import { styles as responsiveStyles } from '../../responsive';
import defaultTheme from '../../theme';
import * as utils from '../../utils';

const stylesTemplate = ({
  top,
  left,
  marginTop: marginTopFromProps,
  marginLeft: marginLeftFromProps,
  theme,
}) => {
  const space = theme.space || defaultTheme.space;
  const paddingTop = typeof top === 'number' ? top : space[top];
  const paddingLeft = typeof left === 'number' ? left : space[left];
  const marginTop =
    typeof marginTopFromProps === 'number'
      ? marginTopFromProps
      : space[marginTopFromProps];
  const marginLeft =
    typeof marginLeftFromProps === 'number'
      ? marginLeftFromProps
      : space[marginLeftFromProps];

  return {
    paddingTop,
    paddingLeft,
    marginTop,
    marginLeft,
  };
};

const rootStylesPropertyTemplate = (property, value, theme) => {
  const space = theme.space || defaultTheme.space;

  if (utils.isObject(value)) {
    const top = typeof value.top === 'number' ? value.top : space[value.top];

    const right =
      typeof value.right === 'number' ? value.right : space[value.right];

    const bottom =
      typeof value.bottom === 'number' ? value.bottom : space[value.bottom];

    const left =
      typeof value.left === 'number' ? value.left : space[value.left];

    return {
      [`${property}Top`]: top,
      [`${property}Right`]: right,
      [`${property}Bottom`]: bottom,
      [`${property}Left`]: left,
    };
  }

  const shorthand = utils
    .mapValueToArray(value)
    .filter(Boolean)
    .reduce((acc, cur) => `${acc} ${utils.mapNumberToUnit(space[cur])}`, '');

  return {
    [property]: shorthand,
  };
};

const rootStylesTemplate = ({ root, rootMargin, theme }) => ({
  ...rootStylesPropertyTemplate('padding', root, theme),
  ...rootStylesPropertyTemplate('margin', rootMargin, theme),
});

export const StyledSpacer = styled.div`
  ${responsiveStyles((props) => ({
    ':not(:empty)': rootStylesTemplate(props),
    '> * + *:not(:empty)': stylesTemplate(props),
  }))}
`;

StyledSpacer.propTypes = {
  /**
   * Top children padding
   */
  top: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  /**
   * Left children padding
   */
  left: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  /**
   * Top children margin
   */
  marginTop: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  /**
   * Left children margin
   */
  marginLeft: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  /**
   * Root padding
   */
  root: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.array,
    PropTypes.shape({
      top: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
      left: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
      right: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
      bottom: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
    }),
  ]),
  /**
   * Root margin
   */
  rootMargin: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.array,
    PropTypes.shape({
      top: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
      left: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
      right: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
      bottom: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
    }),
  ]),
};

StyledSpacer.defaultProps = {
  marginTop: null,
  marginLeft: null,
  top: null,
  left: null,
  root: null,
  rootMargin: null,
};

StyledSpacer.displayName = 'Spacer';

export default StyledSpacer;
