import PropTypes from 'prop-types';
import styled from 'styled-components';
import defaultTheme from '../../theme';
import { styles as responsiveStyles } from '../../responsive';

const stylesTemplate = ({ variant, tint, align, theme }) => {
  const typography = theme.typography || defaultTheme.typography;
  const colors = theme.colors || defaultTheme.colors;
  return {
    ...typography[variant],
    color: colors[tint],
    textAlign: align,
  };
};

export const StyledText = styled.div`
  ${responsiveStyles(stylesTemplate)}
`;

StyledText.propTypes = {
  /**
   * Font size
   */
  variant: PropTypes.string,
  /**
   * Color
   */
  tint: PropTypes.string,
  /**
   * Text aligment
   */
  align: PropTypes.oneOf(['left', 'right', 'center']),
};

StyledText.defaultProps = {
  variant: null,
  tint: null,
  align: null,
};

StyledText.displayName = 'Text';

export default StyledText;
