import { css } from 'styled-components';

export const supportsTouch = (...args) => css`
  @media (hover: none) and (pointer: coarse) {
    ${css(...args)}
  }
`;

export const supportsHover = (...args) => css`
  @media (hover: hover) and (pointer: fine) {
    ${css(...args)}
  }
`;

export const hover = (...args) => css`
  @media (hover: hover) and (pointer: fine) {
    :hover {
      ${css(...args)}
    }
  }
`;

export const landscape = (...args) => css`
  @media screen and (min-aspect-ratio: 14/10) and (orientation: landscape) {
    ${css(...args)}
  }
`;

export const portrait = (...args) => css`
  @media (orientation: portrait) {
    ${css(...args)}
  }
`;

export const preferColorSchemeDark = (...args) => css`
  @media(prefers-color-scheme: dark) {
    ${css(...args)}
  }
`;

export const preferColorSchemeLight = (...args) => css`
  @media(prefers-color-scheme: light) {
    ${css(...args)}
  }
`;
