// eslint-disable-next-line no-nested-ternary
const normalizeFlexAlignValue = (value) => ([
  'start',
  'end',
].includes(value)
  ? `flex-${value}`
  : [
    'between',
    'around',
    'evenly',
  ].includes(value)
    ? `space-${value}`
    : value
);

const mapNumberToUnit = (value, unit = 'px') => (typeof value === 'number'
  ? `${value}${unit}`
  : value);

// eslint-disable-next-line no-nested-ternary
const mapValueToArray = (value) => (Array.isArray(value)
  ? value
  : [value]);

const isObject = (val) => Object.prototype.toString.call(val) === '[object Object]';

export {
  mapNumberToUnit,
  mapValueToArray,
  isObject,
  normalizeFlexAlignValue,
};
