import PropTypes from 'prop-types';
import styled from 'styled-components';
import { normalizeFlexAlignValue } from '../../utils';
import { styles as responsiveStyles } from '../../responsive';

const stylesTemplate = (props) => {
  const data = { ...props };

  if (typeof props === 'number') {
    data.col = props;
  }

  const {
    col,
    margin,
    grow,
    shrink: flexShrink,
    alignSelf,
    index: order,
  } = data;

  const flexGrow = col ? 0 : grow;

  const flexBasis =
    typeof col === 'number' && col > 0
      ? `calc(100% / var(--grid-columns) * ${Math.floor(col)})`
      : col;

  const marginLeft =
    typeof margin === 'number'
      ? `calc(100% / var(--grid-columns) * ${Math.floor(margin)})`
      : null;

  const padding = 'calc(var(--grid-gutter) * 0.5)';

  return {
    paddingLeft: padding,
    paddingRight: padding,
    flexGrow,
    flexShrink,
    flexBasis,
    marginLeft,
    order,
    alignSelf: normalizeFlexAlignValue(alignSelf),
  };
};

export const StyledCol = styled.div`
  box-sizing: border-box;
  ${responsiveStyles(stylesTemplate)}
`;

StyledCol.propTypes = {
  /**
   * Column width
   */
  col: PropTypes.oneOfType([PropTypes.oneOf(['auto']), PropTypes.number]),
  /**
   * Column offset
   */
  margin: PropTypes.number,
  /**
   * Flex-grow
   */
  grow: PropTypes.number,
  /**
   * Flex-shrink
   */
  shrink: PropTypes.number,
  /**
   * Flex-order
   */
  index: PropTypes.number,
  /**
   * Flex align-self
   */
  alignSelf: PropTypes.oneOf(['start', 'center', 'end', 'baseline']),
};

StyledCol.defaultProps = {
  col: 0,
  margin: null,
  grow: 1,
  shrink: 1,
  index: null,
  alignSelf: null,
};

StyledCol.displayName = 'Col';

export default StyledCol;
