/* eslint-disable no-nested-ternary */
import PropTypes from 'prop-types';
import styled from 'styled-components';
import { styles as responsiveStyles } from '../../responsive';
import defaultTheme from '../../theme';
import Row from '../Row/Row';
import Col from '../Col/Col';

const boxShadow = ({ debug, debugColor }) =>
  debug
    ? `
  inset 0 0 0 1px ${debugColor},
  0 0 0 1px ${debugColor}`
    : null;

const stylesTemplate = ({
  gutter: gutterFromProps,
  columns: columnsFromProps,
  masked,
  theme,
}) => {
  const overflowX = typeof masked === 'boolean' && masked ? 'hidden' : null;

  const columnsFromTheme = theme.columns || defaultTheme.columns;

  const gridColumns =
    typeof columnsFromProps === 'number'
      ? columnsFromProps
      : columnsFromTheme[columnsFromProps];

  const gutterFromTheme = theme.gutter || defaultTheme.gutter;

  const gridGutter =
    typeof gutterFromProps === 'number'
      ? gutterFromProps
      : gutterFromTheme[gutterFromProps];

  return {
    '--grid-columns': String(gridColumns),
    '--grid-gutter': gridGutter,
    overflowX,
  };
};

export const StyledGrid = styled.div`
  &,
  ${Row}, ${Col} {
    box-shadow: ${boxShadow};
  }
  ${responsiveStyles(stylesTemplate)}
`;

StyledGrid.propTypes = {
  /**
   * Grid columns
   */
  columns: PropTypes.oneOfType([PropTypes.number, PropTypes.string]),
  /**
   * Gutter between columns
   */
  gutter: PropTypes.oneOfType([PropTypes.number, PropTypes.string]),
  /**
   * Set overflow hidden on parent
   */
  masked: PropTypes.bool,
  /**
   * Apply border on every col
   */
  debug: PropTypes.bool,
  /**
   * Change debug border color
   */
  debugColor: PropTypes.string,
};

StyledGrid.defaultProps = {
  columns: 'default',
  gutter: 'default',
  masked: false,
  debug: false,
  debugColor: 'gray',
};

StyledGrid.displayName = 'Grid';

export default StyledGrid;
