export const backgroundSize = ([cWidth, cHeight], [width, height], mode = 'cover', offset = 0) => {
  const oRatio = width / height;
  const cRatio = cWidth / cHeight;
  const data = {};
  if (mode === 'contain' ? (oRatio > cRatio) : (oRatio < cRatio)) {
    data.width = cWidth;
    data.height = cWidth / oRatio;
  } else {
    data.width = cHeight * oRatio;
    data.height = cHeight;
  }
  data.left = (cWidth - data.width) * (offset / 100);
  data.right = data.width + data.left;
  return data;
};
